body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.imagePicker {    
    padding: 1em 2em;
    background: none;
    background-color: #eee;
    margin: 2em 1em;
    box-shadow: 10px 8px 37px -4px rgba(0,0,0,0.75);
}

.imagePicker h2 {
    background: none;
    color: gray;
}

.imageChoices {
    display: grid;
    background: none;
    grid-gap: .5em;
    grid-template-columns: auto auto auto auto;
    justify-content: space-around;
    margin-top: 2em;
}

img {
    height: 8em;
    width: 8em;
}

/*.selectedPic {
    border: solid .25em #f69d3c;
}*/


@media only screen and (max-width: 600px) {
  .imageChoices {
    grid-template-columns: auto auto;
  }
}

.memePreview {
    display: flex;
    background: none;
    justify-content: space-around;
    border: 5px ridge gray;    
    background-color: #ddd;
    height: 20em;
    width: 20em;
    padding: .5em;
    position: relative;
    color: white;
}

.memePreview img {
    height: 20em;
    width: 20em;
}

.textTop {
    position: absolute;
    top: 1.25em;
    background: none;
    font-size: 2rem;
}

.textBottom {
    position: absolute;
    bottom: 1.25em;
    background: none;
    font-size: 2rem;
}
.textInput {		
    background: none;
    background-color: #eee;
	padding: 1em 6em;
	margin: 2em 1em;
box-shadow: 10px 8px 37px -4px rgba(0,0,0,0.75);
}

.textInput h2 {	
    background: none;
    color: gray;
}

.inputRow {
	display: flex;
	justify-content: space-around;
	background: none;
}

input {
	background: none;
	background-color: #eee;
	font-size: 1rem;
	padding: .25em;
	margin-top: 1em;
}

@media only screen and (max-width: 600px) {
  .inputRow {
    flex-direction: column;
  }
}
.container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: none;
    background: -webkit-linear-gradient(left, #3f87a6, #ebf8e1, #f69d3c);
    background: linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c);
    padding: 2em;
}

h1 {
	font-size: 3rem;
	background: none;
	color: gray;
	  -webkit-text-fill-color: #fff; 
	  -webkit-text-stroke-width: 1px;
	  -webkit-text-stroke-color: gray;

}

* {
    max-width: 960px;
    margin:auto;
    background: -webkit-linear-gradient(#fff, #999);
    background: linear-gradient(#fff, #999);
  }
